body {
    background-color: #ece7e4;
    color: #AC3B69;
    font-family: Arial, Helvetica, sans-serif
}

.wrapper {
    color: #123C69;
    margin-top: 20px;
}

.wrapper ul li {
    padding-top: 5px;
}

#nav {
    display: flex;
    justify-content: center;
    font-size: 36px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    border-bottom: 2px solid;
}


#nav #nav-links li {
    list-style: none;
    text-align: center;
    border-radius: 10px;
    display: inline;
    padding: 10px;
}

@media only screen and (max-width: 768px) {

    #nav {
        justify-content: center;
    }
    #nav #nav-links li {
        flex-wrap: wrap;
        font-size: 20px;
        padding: 0px;
        margin: 0px;
    }

}

a {
    padding: 10px;
}

.active {
    background-color: #BAB2B5;
    border-radius: 5px;
    color: #AC3B69;
    border-bottom: 6px solid #AC3B69;
    transition: 0.4s;
}

a:active {
    color: #AC3B69;
}

h1 {
    font-size: 36px;
}

h3 {
    font-size: 28px;
}

p {
    font-size: 18px;
}

.grid {
    display: flex;
    grid-column-gap: 0;
    justify-content: center
}

.grid-item {
    margin: 20px;
}

.main-image {
    border-radius: 50%;
    max-height: 100px;
    max-width: 100px;
}

.welcome {
    margin-left: 0;
    margin-top: 10px;
}

.row {
    display: flex;
    justify-content: center;
    object-fit: contain
}

.row .row-item {
    display: inline-block;
    padding: 20px;
    justify-content: center;
}

.row img {
    display: inline-block;
    max-height: 200px;
    max-width: 300px;
    padding: 10px;
    border-radius: 8%;
}

@media only screen and (max-width: 768px) {
    .row img {
        display: none;
    }
}

.projects-grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: center;
}

.projects-grid-item {
    background-color: #faf7f5;
    padding: 5px;
    margin: 5px;
    box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
    border-top: 6px solid #AC3B69;
    transition: 0.5s
}

.projects-grid-item:hover {
    -webkit-transform: scale(1.01);
            transform: scale(1.01)
}

.contact-flex-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.contact-flex-item {
    text-align: center;
    max-width: 300px;
    max-height: 500px;
}

.contact-flex-item a {
    text-align: center;
    text-decoration: none;
}

.contact-flex-container img {
    max-height: 80px;
    max-width: 80px;
    margin-left: 20px;
    padding: 20px;
}

#contact-title {
    text-align: center;
}

.fade-enter {
    opacity: 0.1
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 250ms ease-in;
  }

.fade-exit {
    opacity: 1;
  }

.fade-exit.fade-exit-active {
    opacity: 0.01;
    transition: opacity 250ms ease-in;
  }

div.transition-group {
    position: relative;
  }

section.route-section {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }

h1 {
    font-weight: 500;
}

h3 {
    font-weight: 400;
    color: rgb(43, 65, 88)
}

p {
    font-weight: 400;
    color: rgb(46, 72, 100);
    line-height: 2;
}

li {
    line-height: 2
}
